import React, { Component } from 'react';
import {List, ListItem} from 'material-ui/List';
import ContentInbox from 'material-ui/svg-icons/content/inbox';
import ActionGrade from 'material-ui/svg-icons/action/grade';
import ContentSend from 'material-ui/svg-icons/content/send';
import ContentDrafts from 'material-ui/svg-icons/content/drafts';
import Divider from 'material-ui/Divider';
import ActionInfo from 'material-ui/svg-icons/action/info';
import '../../styles/My.css';
import AppBar from "material-ui/AppBar/index";
import {setDayList, setHomeMSG, setAccount} from "../../redux/Action";
import connect from "react-redux/es/connect/connect";
import FlatButton from "material-ui/FlatButton/index";

class My extends Component {
    render() {
        return (
            <div class="My">
                <AppBar
                    title="我的"
                    iconClassNameRight="muidocs-icon-navigation-expand-more"
                />
                <List>
                    <ListItem primaryText="我的预约" leftIcon={<ActionGrade />} onClick={() => {
                        if (this.props.uuid) {
                            this.props.history.push('/OrderList');
                        } else {
                            this.props.history.push('/Login');
                        }
                    }}/>
                    <ListItem primaryText="收件箱" leftIcon={<ContentInbox />} />
                    <ListItem primaryText="发邮件" leftIcon={<ContentSend />} />
                    <ListItem primaryText="草稿箱" leftIcon={<ContentDrafts />} />
                </List>
                <Divider />
                {/*<List>*/}
                    {/*<ListItem primaryText="All mail" rightIcon={<ActionInfo />} />*/}
                    {/*<ListItem primaryText="Trash" rightIcon={<ActionInfo />} />*/}
                    {/*<ListItem primaryText="Spam" rightIcon={<ActionInfo />} />*/}
                    {/*<ListItem primaryText="Follow up" rightIcon={<ActionInfo />} />*/}
                {/*</List>*/}
                <div style={{padding: 20,}}>
                    <FlatButton class="login-Btn" rippleColor={'#FFF'} style={{marginTop: 50,}} label="退出登陆" fullWidth={true} onClick={() => {
                        this.props.setAccount({})
                        alert('退出登陆成功');
                    }}/>
                </div>
            </div>
        );
    }
}


const mapStateToProps = (state) => {
    return {
        // userName: state.getIn(['account', 'userName']),
        uuid: state.getIn(['account', 'uuid']),
        // dayList: state.getIn(['dayList']),
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        setDayList(data) {
            dispatch(setDayList(data));
        },
        setHomeMSG(data) {
            dispatch(setHomeMSG(data))
        },
        setAccount(data) {
            dispatch(setAccount(data))
        }
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(My);

