import React, { Component } from 'react';
import {List, ListItem} from 'material-ui/List';
import ContentInbox from 'material-ui/svg-icons/content/inbox';
import ActionGrade from 'material-ui/svg-icons/action/grade';
import ContentSend from 'material-ui/svg-icons/content/send';
import ContentDrafts from 'material-ui/svg-icons/content/drafts';
import Divider from 'material-ui/Divider';
import ActionInfo from 'material-ui/svg-icons/action/info';
import '../../styles/OrderList.css';
import AppBar from "material-ui/AppBar/index";
import axios from "axios";
import api from "../../api";
import {setDayList, setHomeMSG} from "../../redux/Action";
import connect from "react-redux/es/connect/connect";
import IconButton from "material-ui/IconButton/index";
import NavigationClose from "material-ui/svg-icons/navigation/close";
import Checkbox from "material-ui/Checkbox/index";
import Paper from "material-ui/Paper/index";
import FlatButton from "material-ui/FlatButton/index";

class OrderList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            CommunityList: []
        }
    }

    componentDidMount() {
        this._getCommunityList();
    }

    _getCommunityList = () => {
        const _this = this;
        axios.post(api.api + 'getCommunityById', {
            communityUserID: this.props.uuid,
        }).then(function (response) {
            console.log(response);
            if ( response.data.code === 201) {
                _this.setState({
                    CommunityList: response.data.data,
                })
            } else {
                alert(response.data.msg);
                _this.props.history.goBack();
            }
        }).catch(function (error) {
            console.log(error);
        });
    }

    _deletCommunity = (communityID) => {
        const _this = this;
        axios.post(api.api + 'deleteCommunity', {
            communityUserID: this.props.uuid,
            communityID: communityID,
        }).then(function (response) {
            console.log(response);
            if ( response.data.code === 201) {
                alert(response.data.msg);
                _this._getCommunityList();
            } else {
                alert(response.data.msg);
                _this.props.history.goBack();
            }
        }).catch(function (error) {
            console.log(error);
        });
    }


    render() {
        return (
            <div class="OrderList">
                <AppBar title={'登陆'} iconElementLeft={<IconButton>
                    <NavigationClose onClick={() => {
                        this.props.history.goBack();
                    }}
                    />
                </IconButton>} />
                <Paper class="Home-table">
                    <div class="OrderList-table-item">
                        <div>时间</div>
                        <div>备注</div>
                        <div></div>
                    </div>
                    <Divider />
                    {this.state.CommunityList.map( (v, i) => {
                        return (
                            <div>
                                <div className="OrderList-table-item">
                                    <div>{`${v.communityUseYear}-${v.communityUseMonth}-${v.communityUseDay} ${parseInt(v.communityUseHour)}:00~${parseInt(v.communityUseHour)+1}:00`}</div>
                                    <div>{v.note}</div>
                                    <div className={"OrderList-table-item-btn"} onClick={() => {
                                        this._deletCommunity(v.communityID);
                                    }}>
                                        取消预约
                                    </div>
                                </div>
                                <Divider />
                            </div>
                        )
                    })}
                </Paper>
                <Divider />
                {/*<List>*/}
                    {/*<ListItem primaryText="All mail" rightIcon={<ActionInfo />} />*/}
                    {/*<ListItem primaryText="Trash" rightIcon={<ActionInfo />} />*/}
                    {/*<ListItem primaryText="Spam" rightIcon={<ActionInfo />} />*/}
                    {/*<ListItem primaryText="Follow up" rightIcon={<ActionInfo />} />*/}
                {/*</List>*/}
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        // userName: state.getIn(['account', 'userName']),
        uuid: state.getIn(['account', 'uuid']),
        // dayList: state.getIn(['dayList']),
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        setDayList(data) {
            dispatch(setDayList(data));
        },
        setHomeMSG(data) {
            dispatch(setHomeMSG(data))
        }
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(OrderList);

