import React, { Component } from 'react';
import AppBar from 'material-ui/AppBar';
import IconButton from 'material-ui/IconButton';
import NavigationClose from 'material-ui/svg-icons/navigation/close';
import Divider from 'material-ui/Divider';
import Paper from 'material-ui/Paper';
import TextField from 'material-ui/TextField';
import Snackbar from 'material-ui/Snackbar';
import '../../styles/Login.css';
import FlatButton from 'material-ui/FlatButton';
import { connect } from 'react-redux'
import { setAccount } from '../../redux/Action'
import axios from 'axios';
import api from './../../api';
import { Map } from 'immutable';


class Sign extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: '',
            password: '',
            againPassword: '',
            barState: false,
            msg: null,
            invite: ''
        }
    }

    handleClick = () => {
        this.setState({
            barState: true,
        });
    };

    handleRequestClose = () => {
        this.setState({
            barState: false,
        });
    };

    _noNull = () => {
        this.setState({
            barState: true,
            msg: '不能为空',
        })
    }

    _signSubmitAxios = () => {
        const _this = this;
        axios.post(api.api + 'addUser', {
            userName: this.state.userName,
            password: this.state.password,
            invite: this.state.invite,
        }).then(function (response) {
            console.log(response);
            if ( response.data.code === 201) {
                _this.setState({
                    barState: true,
                    msg: '注册成功',
                });
                setTimeout(() => {
                    _this.props.history.goBack();
                }, 800)
            } else {
                _this.setState({
                    barState: true,
                    msg: response.data.msg,
                })
            }
        }).catch(function (error) {
            console.log(error);
        });
    }

    _signSubmit = () =>{
        const {
            userName,
            password,
            againPassword,
            barState,
            msg,
            invite,
        } = this.state
        if (!userName) {
            this._noNull();
            return;
        } else if (!password) {
            this._noNull();
            return;
        } else if (!againPassword) {
            this._noNull();
            return;
        } else if (!invite) {
            this._noNull();
            return;
        } else if (password !== againPassword) {
            this.setState({
                barState: true,
                msg: '两次输入密码不一致',
            });
            return;
        }

        this._signSubmitAxios();
    }

    render() {
        return (
            <div className="Login">
                <AppBar title={'注册'} iconElementLeft={<IconButton>
                    <NavigationClose onClick={() => {
                        this.props.history.goBack();
                    }}
                    />
                </IconButton>} />
                <div class="Login-Main">
                    <Paper zDepth={2}>
                        <TextField hintText="请输入您的账号" style={{marginLeft: 20,}}  underlineShow={false} onChange={(event, newValue) => {
                            console.log(newValue);
                            this.setState({
                                userName: newValue,
                            })
                        }} />
                        <Divider />
                        <TextField hintText="请输入您的密码" type="password" style={{marginLeft: 20,}}  underlineShow={false} onChange={(event, newValue) => {
                            this.setState({
                                password: newValue,
                            })
                        }}  />
                        <Divider />
                        <TextField hintText="请再次输入您的密码" type="password" style={{marginLeft: 20,}}  underlineShow={false} onChange={(event, newValue) => {
                            this.setState({
                                againPassword: newValue,
                            })
                        }}  />
                        <Divider />
                        <TextField hintText="请再次输入邀请码" type="password" style={{marginLeft: 20,}}  underlineShow={false} onChange={(event, newValue) => {
                            this.setState({
                                invite: newValue,
                            })
                        }}  />
                        <Divider />
                    </Paper>
                    <FlatButton class="login-Btn" rippleColor={'#FFF'} style={{marginTop: 150,}} label="注册" fullWidth={true} onClick={this._signSubmit}/>
                </div>
                <Snackbar
                    open={this.state.barState}
                    message={this.state.msg}
                    autoHideDuration={4000}
                    onRequestClose={this.handleRequestClose}
                />
            </div>
        );
    }
}

const mapStateToProps = (state) => {

}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        setAccount(data) {
            dispatch(setAccount(data));
        }
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Sign);
